<template>
  <van-form @submit="onSubmit" style="position: absolute;top: 20%;width:100%">
    <van-field
      v-model="username"
      name="username"
      label="用户名"
      placeholder="用户名"
      :rules="[{ required: true, message: '请填写用户名' }]"
    />
    <van-field
      v-model="password"
      type="password"
      name="password"
      label="密码"
      placeholder="密码"
      :rules="[{ required: true, message: '请填写密码' }]"
    />
    <div style="margin: 16px;">
      <van-button round block type="info" native-type="submit">提交</van-button>
    </div>
  </van-form>
</template>

<script>
import Vue from "vue";
import { Form,Field,Button } from "vant";
import {instance2} from '@/utils/http'
import { Notify } from 'vant';
import {mapMutations} from 'vuex'

// 全局注册
Vue.use(Notify);
Vue.use(Button);
Vue.use(Field);
Vue.use(Form);
export default {
    data(){
        return{
            username:'',
            password:''
        }
    },
    created(){
        this.changeIsTabbar(false)
    },
    methods:{
        ...mapMutations('TabbarStore',['changeIsTabbar']),
        onSubmit(data){
            instance2.post('/api/user/loginin',data).then(res=>{
                if(res.data.flag){
                    Notify({ type: 'success', message: '欢迎您！' + res.data.data.username });
                    localStorage.setItem('token',res.data.data.token)
                    this.$router.push('/center')
                }else{
                    Notify({ type: 'danger', message: res.data.data.message });
                }
            }).catch(err=>console.log(err))
        }
    }
};
</script>
